@import url(https://fonts.googleapis.com/css2?family=Otomanopee+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{

font-size:15px ;
font-family: 'Otomanopee One', sans-serif;

}
@-webkit-keyframes dropdown {
  from{ max-height: 0rem;}
  to{max-height: 6rem;}
}
@keyframes dropdown {
  from{ max-height: 0rem;}
  to{max-height: 6rem;}
}
@-webkit-keyframes dropup {
  from{ max-height: 6rem;}
  to{max-height: 0rem;}
}
@keyframes dropup {
  from{ max-height: 6rem;}
  to{max-height: 0rem;}
}


/* width */
.skillDropdown::-webkit-scrollbar {
  width: 10px;
}

/* Track */
.skillDropdown::-webkit-scrollbar-track {
}

/* Handle */
.skillDropdown::-webkit-scrollbar-thumb {
  background: #2E2E3A;
  border-radius: 1px;
}


.CenterDiv{
  background-color: #DE9151;
  position: absolute;
  width: 100%;
  height: 100%;
  
}
.CenterSvg{
  height: 100%;
  width: 100%;
}

body{
  overflow: hidden;
}
.App{
  display: flex;
  flex-direction: row;
  z-index: 0;
  width: 100%;
  position: relative;
  height: 100vh;
}
.Left{
  position: absolute;
  z-index: 2;
  top: 10%;
  display: flex;
  flex-direction: column;

  left: 1rem;
  background-color: #763A1D;
  /* background-color: #f45a2f86; */
  border: 5px solid #2E2E3A;
  max-height: 28rem;
  border-radius: 6px;
  width: 18rem;
  padding: 1rem;
}
.Right{
  position:absolute;
  z-index: 2;
  top: 10%;
  display: flex;
  flex-direction: column;

  right: 1rem;
  background-color: #ffedc8;
  border: 5px solid #2E2E3A;
  height: 50%;
  border-radius: 6px;
  width: 10rem;
  padding: 1rem;

}
.divSkill{

  max-height: 10rem;
 opacity: 1;
  display: flex;
  flex-direction: column;
  padding-left:2rem;
}
.beforeInput{

  display: flex;
  flex-direction: row;
  margin-right: 1px;
  justify-content: center;
  align-items: baseline;
  width: 100%;
  right: 1rem;
  position:relative;


}
.beforeInput>div{
  margin: 5px;
  font-size: 1.3rem;

  margin-bottom: 0px;
  width: 80%;
}
.skillDropdown{
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  max-height: 0rem;
  width: 85%;

}
.skillDropdown>div{
  padding:0.2rem;
  border-bottom: 3px solid;
  border-color:#763A1D ;
  background-color: white;
  z-index: 3;
  width: 100%;

}
.beforeInput>div>div>input{
  justify-self: stretch;
  height: 2rem;
  z-index:2;
  width: 100%;
  background-color: white;
  border-bottom:  3px solid #2E2E3A;
  border-right:  3px solid #2E2E3A;}


.afterInput{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: relative;

}
.afterInput>div{
  background-color: white;
  position:absolute;
  z-index:2;

  left: 75%;
}
.afterInput>div>img{
  width: 1rem;
  display: block;
  padding: 0.3rem;

  padding-top: 0.7rem;
}
#DivTitle{
  position:absolute;
  z-index: 1;
}
#DivTitle>p{
  position: absolute;
  font-family: 'Otomanopee One', sans-serif;
  color: #1C1C24;
  background-color: #BC5D2E;
  max-width: 10rem;
  padding: 0.2rem;
  border-radius: 6px;
  font-size: 1rem;
}
.Next{
  font-family: 'Work Sans', sans-serif;
  background-color: white;
  font-size: 1.5rem;
  border-radius: 3px;
  border:0px;
  border-bottom:  3px solid #2E2E3A;
  border-right:  3px solid #2E2E3A;


}
#Tag1{
  position:absolute;
  background-color: #BC5D2E;
  padding: 0.3rem;
  z-index:1;
  top:42%;
  padding-top:0.8rem;
  border-bottom:  3px solid #2E2E3A;
  border-right:  3px solid #2E2E3A;
  border-left:  3px solid #2E2E3A;
  border-radius: 3px;
  border-bottom-left-radius: 9px;
  border-bottom-right-radius: 9px;
  color:white;


}
#Tag2{
  position:absolute;
  background-color: #BC5D2E;
  padding: 0.3rem;
  z-index:1;
  top:42%;
  padding-top:0.8rem;
  border-bottom:  3px solid #2E2E3A;
  border-right:  3px solid #2E2E3A;
  border-left:  3px solid #2E2E3A;
  border-radius: 3px;
  border-bottom-left-radius: 9px;
  border-bottom-right-radius: 9px;
  color:white;


}
.divSkill>div{
  padding-bottom: 1rem;

}






@media only screen and (max-width: 600px) {
 html{
  font-size:9px ;
  font-family: 'Otomanopee One', sans-serif;  }
}
